import {AfterViewInit, Component, TemplateRef, ViewChild, ViewEncapsulation} from '@angular/core';
import {NgbModal, NgbModalRef} from '@ng-bootstrap/ng-bootstrap';
import {KeyboardShortcutsComponent, ShortcutInput} from 'ng-keyboard-shortcuts';

@Component({
  selector: 'app-layout',
  templateUrl: './tastaturbedienbarkeit.component.html',
  encapsulation: ViewEncapsulation.None
})

export class TastaturbedienbarkeitComponent implements AfterViewInit {

  shortcuts: ShortcutInput[] = [];
  modal: NgbModalRef;

  @ViewChild(KeyboardShortcutsComponent) private keyboard: KeyboardShortcutsComponent;
  @ViewChild('content') templateRef: TemplateRef<any>;
  private modalRef: NgbModalRef;

  constructor(private modalService: NgbModal) { }

  ngAfterViewInit(): void {
    this.shortcuts.push(
      {
        key: '?',
        command: () => this.showModal(),
        preventDefault: true
      },
      {
        key: 'h',
        command: () => this.showModal(),
        preventDefault: true
      },
      {
        key: ['k s'],
        command: () => this.showModal(),
        preventDefault: true
      }
    );
  }

  showModal() {
    if (this.modalService.hasOpenModals()){
      this.modal.close();
    }
    this.modal = this.modalService.open(this.templateRef);
  }

}
