import {Component, forwardRef, HostBinding, Injectable, Input, ViewChild} from '@angular/core';
import {
  NgbCalendar,
  NgbDateParserFormatter,
  NgbDatepickerI18n,
  NgbDateStruct,
  NgbInputDatepicker
} from '@ng-bootstrap/ng-bootstrap';
import {ControlValueAccessor, NG_VALUE_ACCESSOR} from '@angular/forms';
import {DatepickerI18n} from './datepicker.i18n';

@Injectable()
export class SwissDateParserFormatter extends NgbDateParserFormatter {

  parse(value: string): NgbDateStruct | null {
    if (value) {
      const date = value.split('.');
      return {
        day: parseInt(date[0], 10),
        month: parseInt(date[1], 10),
        year: parseInt(date[2], 10)
      };
    }
    return null;
  }

  format(date: NgbDateStruct | null): string {
    return date ? `${date.day}.${date.month}.${date.year}` : '';
  }
}

@Component({
  selector: 'app-datepicker',
  templateUrl: './datepicker.component.html',
  providers: [
    {
      provide: NG_VALUE_ACCESSOR,
      useExisting: forwardRef(() => DatepickerComponent),
      multi: true
    },
    {
      provide: NgbDateParserFormatter,
      useClass: SwissDateParserFormatter
    },
    {
      provide: NgbDatepickerI18n,
      useClass: DatepickerI18n
    }
  ]
})
export class DatepickerComponent implements ControlValueAccessor {

  @HostBinding('attr.id')
  externalId = '';

  @ViewChild(NgbInputDatepicker, {static: true})
  datepicker: NgbInputDatepicker;

  @ViewChild('dateButton')
  button: HTMLButtonElement;

  private _value;

  public get value() {
    return this._value;
  }

  public set value(v) {
    this._value = v;
    this.onChange(this._value);
    this.onTouched();
  }

  private _id = '';

  @Input()
  set id(value: string) {
    this._id = value;
    this.externalId = null;
  }

  get id() {
    return this._id;
  }

  constructor(private calendar: NgbCalendar) {
  }

  selectToday() {
    this.value = this.calendar.getToday();
    this.datepicker.navigateTo(this.value);
  }

  /**
   * Empty listener, to be overwritten by 'registerOnChange'
   */
  onChange: (_: any) => void = (_: any) => {
  };
  /**
   * Empty listener, to be overwritten by 'registerOnTouched'
   */
  onTouched: () => void = () => {
  };

  registerOnChange(fn: any): void {
    this.onChange = fn;
  }

  registerOnTouched(fn: any): void {
    this.onTouched = fn;
  }

  setDisabledState(isDisabled: boolean): void {
    this.datepicker.disabled = isDisabled;
    this.button.disabled = isDisabled;
  }

  writeValue(obj: any): void {
    this.value = obj;
    this.onChange(this.value);
  }

}
