import {Component, EventEmitter, Input, Output, ViewEncapsulation} from '@angular/core';
import {Router} from '@angular/router';

@Component({
  selector: 'app-functionbar-item',
  template: `
    <li class="nav-item">
      <button type="button" class="btn btn-light btn-functionbar btn-lg nav-item"
              [attr.title]="tooltipKey | translate"
              [attr.aria-label]="labelKey ? null : (tooltipKey | translate)"
              [disabled]="disabled" (click)="onclick($event)">
        <i *ngIf="icon" class="{{icon}} fa-fw" aria-hidden="true"></i>
        <small *ngIf="labelKey">{{labelKey | translate}}</small>
      </button>
    </li>
  `,
  encapsulation: ViewEncapsulation.None
})
export class FunctionbarItemComponent {

  @Input() routerLink: string;
  @Input() icon: string;
  @Input() tooltipKey: string;
  @Input() labelKey: string;

  @Input() disabled = false;

  @Output() itemClicked = new EventEmitter<void>();

  constructor(private router: Router) {
  }

  onclick(event: MouseEvent) {
    this.itemClicked.emit();
    if (this.routerLink) {
      this.router.navigate([this.routerLink]);
    }
    event.stopPropagation();
    // the (click) subscribers have also subscribed to the DOM event, and we don't want them to be invoked twice ...
  }
}
