import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {DemoStatus} from '../../../demo-utils/status/demo-status.directive';

@Component({
  selector: 'app-form-examples',
  templateUrl: './large-form-example.component.html',
  encapsulation: ViewEncapsulation.None
})
export class LargeFormExampleComponent implements OnInit {
  maxWidth = '100%';

  issue = {
    subject: null
  };
  demoStatus = DemoStatus;
  date?: '2004-06-02';
  status = this.demoStatus.ok;
  invalid: boolean;
  readOnly: boolean;

  constructor() {
  }

  ngOnInit() {
    this.switchStatus(this.demoStatus.ok);
  }

  switchStatus(s: DemoStatus) {
    this.status = s;
    this.invalid = this.status === DemoStatus.invalid;
    this.readOnly = this.status === DemoStatus.readOnly;
  }
}
