import {Component, OnInit, ViewEncapsulation} from '@angular/core';
import {ADDRESS_DATA, AddressData} from './address-data';
import * as _ from 'underscore';
import {NgbModal, NgbModalRef} from '@ng-bootstrap/ng-bootstrap';
import {AlertHolderDirective} from '../../../bedag-components/alert/alert-holder.directive';

@Component({
  selector: 'app-tables',
  templateUrl: './datatables.component.html',
  encapsulation: ViewEncapsulation.None
})
export class DatatablesComponent implements OnInit {
  maxWidth = '100%';

  addressData = [];
  pagedItems: Array<AddressData> = [];
  selected: number[] = [];
  sortedBy: string;
  sortedAscending: boolean;
  searchTerm: string;
  page = 1;
  pageSize = 5;
  pageSizes = [5,10,25,100];
  modal: NgbModalRef;
  selectAll: boolean;
  selectedForDetail: AddressData;

  constructor(private modalService: NgbModal, private alerts: AlertHolderDirective) {
  }

  ngOnInit() {
    this.addressData = ADDRESS_DATA;
    this.sortBy('name');
  }

  getColumnClassNames(column: string) {
    let classNames = 'fas fa-sort'
    if (this.sortedBy === column){
      classNames += this.sortedAscending ? '-up' : '-down';
      classNames += ' text-primary';
    }
    return classNames;
  }

  sortBy(field: string) {
    if (this.sortedBy === field) {
      this.sortedAscending = !this.sortedAscending;
      this.addressData.reverse();
    } else {
      this.sortedBy = field;
      this.sortedAscending = true;
      this.addressData = _.sortBy(this.addressData, field);
    }

    this.setPage(1);
    return false; // do not follow the href
  }

  search() {
    this.addressData = ADDRESS_DATA.filter(item => JSON.stringify(item).toLowerCase().indexOf(this.searchTerm.toLowerCase()) !== -1);

    if (this.sortedBy) {
      this.addressData = _.sortBy(this.addressData, this.sortedBy);

      if (!this.sortedAscending) {
        this.addressData.reverse();
      }
    }

    this.setPage(1);
  }

  setPage(page: number) {
    this.page = page;
    const startIndex = (this.page - 1) * this.pageSize;
    const endIndex = Math.min(startIndex + this.pageSize, this.addressData.length);
    this.pagedItems = this.addressData.slice(startIndex, endIndex);
    this.selected = [];
    this.selectAll = false;
  }

  showModal(content, index: number) {
    this.selectedForDetail = this.pagedItems[index];
    this.modal = this.modalService.open(content);
  }

  isSelected(i: number) {
    return this.selectAll || this.selected.includes(i);
  }

  toggleSelection(i: number) {
    if (this.selected.includes(i)){
      this.selected = this.selected.filter(n => n !== i);
    } else {
      this.selected.push(i);
    }
  }

  toggleSelectAll(){
    this.selectAll = !this.selectAll;
    if (!this.selectAll) {
      this.selected = [];
    }
  }

  showAlert(address: AddressData, alertText: string) {
    this.alerts.success(alertText + ' wurde ausgewählt für ' + address.vorname + ' ' + address.name);
  }

  processSelected(action: string) {
    if (!this.selectAll && this.selected.length < 1){
      this.alerts.success('Keine Einträge ausgewählt')
    } else {
    let text = action + ' ';
      if (this.selectAll){
        text += this.pagedItems.map(a => a.vorname + ' ' + a.name).join(', ');
      } else {
        text += this.selected.map(i => this.pagedItems[i].vorname + ' ' + this.pagedItems[i].name).join(', ');
      }
      this.alerts.success(text);
    }
  }
}
