import {Directive, ElementRef, Input, OnChanges, OnInit, Renderer2, SimpleChanges} from '@angular/core';

const ALL_CLASSES = ['is-invalid', 'is-read-only'];

/**
 * Hack Directive, only for demo purposes. Definitely not a best practice :>)
 */
// tslint:disable-next-line:directive-selector
@Directive({selector: '[demo-status]'})
export class DemoStatusDirective implements OnChanges, OnInit {
  @Input()
  status: DemoStatus;
  private label: any;
  private inputId: string;

  constructor(private element: ElementRef, private r2: Renderer2) {

  }

  ngOnInit(): void {
    this.inputId = this.element.nativeElement.id;
    setTimeout(() => this.resetStatus(), 1000);
  }

  /*private loadLabel() {
    if (this.label && this.label.classList) {
      return;
    }
    this.label = document.querySelector(`label[for="${this.inputId}"]`);
  }*/

  setDisabled(d: boolean) {
    this.element.nativeElement.disabled = d;
  }

  addClass(c: string) {
    this.r2.addClass(this.element.nativeElement, c);
    /*if (this.label && this.label.classList) {
      this.label.classList.add(c);
    }*/
  }

  ngOnChanges(changes: SimpleChanges): void {
    const status = changes.status;
    if (!status) {
      return;
    }

    this.resetStatus();
  }

  private resetStatus() {
    /*this.loadLabel();
    if (!(this.label && this.label.classList)) {
      return;
    }*/
    ALL_CLASSES.forEach(a => {
      this.r2.removeClass(this.element.nativeElement, a);
      /*this.label.classList.remove(a);*/
    });
    let d = false;

    switch (this.status) {
      case DemoStatus.invalid:
        this.addClass('is-invalid');
        break;
      case DemoStatus.readOnly:
        this.addClass('is-read-only');
        d = true;
        break;
      default:
        break;
    }
    this.setDisabled(d);
  }
}

export enum DemoStatus {
  ok = 'ok',
  invalid = 'invalid',
  readOnly = 'readOnly'
}
