import {Component} from '@angular/core';
import {ModalDismissReasons, NgbModal, NgbModalOptions, NgbModalRef} from '@ng-bootstrap/ng-bootstrap';

@Component({
  templateUrl: './modal-dialog.component.html',
})
export class ModalDialogComponent {
  assignees = ['Hans Olo', 'Paul Panther'];

  constructor(private modalService: NgbModal) { }
  modal: NgbModalRef;
  closeResult: string;

  smallDialogOptions: NgbModalOptions = {
    size: 'sm',
  };

  largeDialogOptions: NgbModalOptions = {
    size: 'lg',
    backdrop: 'static',
    keyboard: false
  };

  private static getDismissReason(reason: any): string {
    if (reason === ModalDismissReasons.ESC) {
      return 'by pressing ESC';
    } else if (reason === ModalDismissReasons.BACKDROP_CLICK) {
      return 'by clicking on a backdrop';
    } else {
      return  `with: "${reason}"`;
    }
  }

  open(content, size) {
    const dialogOptions = (size === 'lg') ? this.largeDialogOptions : this.smallDialogOptions;
    this.modal = this.modalService.open(content, dialogOptions );
    this.modal.result.then((result) => {
      this.closeResult = `Closed with: "${result}"`;
    }, (reason) => {
      this.closeResult = `Dismissed ${ModalDialogComponent.getDismissReason(reason)}`;
    });
  }

  saveIssue() {
    this.modal.close('Anfrage erstellen')
  }

  closeYear() {
    this.modal.close('Jahr abschliessen')
  }
}
