import {Component, ViewEncapsulation} from '@angular/core';
import {Observable} from 'rxjs';
import {debounceTime, distinctUntilChanged, filter, map} from 'rxjs/operators';

import data from './data.json'

const MAX_SEARCH_RESULTS = 10;

@Component({
  selector: 'app-buttons',
  templateUrl: './autocompletion-demo.component.html',
  encapsulation: ViewEncapsulation.None
})
export class AutocompletionDemoComponent {
  private countries: string[] = data.filter(t => t.type === 'Land').map(t => t.shortNameDe)
  public country: string;

  public item: Item;


  /** EXAMPLE Simple search method
   * Results that start with the exact search term will be shown first.
   * This search algorithm is suited for data sets consisting of single words.
   * For a more complete algorithm see below.
   */
  searchCountries = (text$: Observable<string>) => text$.pipe(
    debounceTime(200),
    distinctUntilChanged(),
    filter(term => term.length > 0),
    map(
      term => {
        const normalizedTerm = term.toLowerCase().trim();
        const startsWith = this.countries.filter(c => c.toLowerCase().startsWith(normalizedTerm)).slice(0, MAX_SEARCH_RESULTS);
        const remaining = MAX_SEARCH_RESULTS - startsWith.length;
        if (remaining === 0) {
          return startsWith;
        }
        const contains = this.countries.filter(c => c.toLowerCase().indexOf(normalizedTerm) > 0).slice(0, remaining);
        return startsWith.concat(contains);
      }
    )
  )

  /** EXAMPLE Complex search method
   * This search algorithm is suited for data sets consisting of multiple words.
   * 1st priority: Results that start with the exact search term.
   * 2nd priority: Results that contain a word, starting with the exact search term.
   * 3rd priority: Results containing the search term anywhere.
   */
  searchLocation = (text$: Observable<string>) => text$.pipe(
    debounceTime(200),
    distinctUntilChanged(),
    filter(term => term.length > 0),
    map(
      term => {
        const normalizedTerm = term.toLowerCase().trim();
        let result = data
        .filter(c => this.itemStartsWith(c as Item, normalizedTerm))
        .slice(0, MAX_SEARCH_RESULTS);
        let remaining = MAX_SEARCH_RESULTS - result.length;

        if (remaining > 0) {
          const tokenMatches = data
          .filter(c => !result.includes(c))
          .filter(c => this.itemTokenMatch(c as Item, normalizedTerm))
          .slice(0, MAX_SEARCH_RESULTS);
          result = result.concat(tokenMatches);
          remaining = MAX_SEARCH_RESULTS - result.length;
        }

        if (remaining > 0) {
          const contains = data
          .filter(c => !result.includes(c))
          .filter(c => this.itemMatches(c as Item, normalizedTerm))
          .slice(0, remaining);
          result = result.concat(contains);
        }
        return result;
      }
    )
  )

  private itemStartsWith(item: Item, term: string) {
    return item.shortNameDe.toLowerCase().startsWith(term);
  }

  private itemTokenMatch(item: Item, term: string) {
    const terms = term.split(/\s+/)
    const tokens = item.shortNameDe.toLowerCase().split(/\s+/)
    let tokenIndex = -1
    for (const termToken of terms) {
      let found = false;
      for (let i = tokenIndex + 1; i < tokens.length; i++) {
        if (tokens[i].startsWith(termToken)) {
          tokenIndex = i;
          found = true;
          break;
        }
      }
      if (!found) {
        return false;
      }
    }
    return true;
  }

  private itemMatches(item: Item, term: string) {
    return item.shortNameDe.toLowerCase().indexOf(term) >= 0;
  }

  formatItem(item: Item): string {
    return `${item.shortNameDe}`;
  }
}

class Item {
  constructor(
    public type: string,
    public shortNameDe: string
  ) {
  }
}
