import {Component} from '@angular/core';
import {AlertHolderDirective} from '../../../bedag-components/alert/alert-holder.directive';

@Component({
  selector: 'app-alert-demo',
  template: `
    <div class="row">
      <div class="col-8 d-flex">
        <button class="btn btn-outline-primary w-100 mr-2" (click)="infoAlert()">
          <span class="fas fa-info-circle text-info mr-2"></span>Alert anzeigen
        </button>
        <button class="btn btn-outline-primary w-100 mr-2" (click)="successAlert()">
          <span class="fas fa-check-circle text-success mr-2"></span>Alert anzeigen
        </button>
        <button class="btn btn-outline-primary w-100 mr-2" (click)="warningAlert()">
          <span class="fas fa-exclamation-triangle text-warning mr-2"></span>Alert anzeigen
        </button>
        <button class="btn btn-outline-primary w-100" (click)="dangerAlert()">
          <span class="fas fa-exclamation-circle text-danger mr-2"></span>Alert anzeigen
        </button>
      </div>
    </div>
  `,
  styleUrls: ['alert-demo.component.scss']
})
export class AlertDemoComponent {

  constructor(private alerts: AlertHolderDirective) {
  }

  infoAlert() {
    this.alerts.info('Der Server steht wegen Wartungsarbeiten von 20.30 bis 24.00 Uhr nicht zur Verfügung.');
  }

  successAlert() {
    this.alerts.success('Das Ticket wurde erstellt.');
  }

  dangerAlert() {
    this.alerts.danger('Das Ticket konnte nicht gespeichert werden.');
  }

  warningAlert() {
    this.alerts.warning('Automatische Updates wurden deaktiviert.');
  }
}
